*{
   
    font-family:"Reem Kufi, sans-serif";
    font-weight: 700;
    font-size: 20px;
    margin: 0px;
   
    text-align:center;
    
  }

  #abc{
    width:100%;
    height:100vh;
    background-image: url("../img/videoplayback.gif");
    background-size: cover;
  }
 
  nav{
    width: 100%;
    height: 70px;
    background-color: rgba(197, 197, 197, 0.333);
    line-height: 50px;
  }
  nav ul{
    float: right;
    margin-right: 30px;
  }
  nav ul li{
    list-style-type: none;
    display: inline-block;
    transition: 0.7s all;
    margin-top: 5px;
    
  }
  nav ul li:hover{
    background-color:rgb(95, 194, 201);
  }
  nav ul li a{
    text-decoration: none;
    color: #fff;
    padding: 30px;
  }

